body {
    font-family: 'Roboto', Arial, Helvetica, sans-serif !important;
    font-size: 140%
}

/* CSS Grid styling via https://gridbyexample.com/patterns/ - Rachel Andrew @rachelandrew */

*,
*:before,
*:after {
    box-sizing: border-box;
}

h1,
p {
    margin: 0 0 1em 0;
}

.centred-content {
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center
}

.wrapper {
    max-width: 940px;
    margin: 0 20px;
    display: grid;
    grid-gap: 10px;
}

.bold-header-uppercase {
    font-weight: 800;
    text-transform: uppercase;
}

@media screen and (min-width: 500px) {
    .sidebar {
        float: left;
        width: 19.1489%;
    }
    .content {
        float: right;
        width: 79.7872%;
    }
    .wrapper {
        margin: 20px auto;
        grid-template-columns: 1fr 3fr;
    }
    .header,
    .footer {
        grid-column: 1 / -1;
        clear: both;
    }
}

.footer {
    width: 100%;
    overflow: hidden;
}

.wrapper>* {
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 10px;
}

@supports (display: grid) {
    .wrapper>* {
        width: auto;
        margin: 0;
    }
}

.content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    overflow: hidden;
}

.content-row-1 {
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex-grow: 1;
            flex-grow: 1
}

.content-row-2 {
    -webkit-align-items: flex-end;
            align-items: flex-end;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    overflow: hidden;
}

.small-text {
    font-size: 16px;
    margin-bottom: 30px;
}

.small-text a {
    text-decoration: none
}

img {
    -webkit-filter: drop-shadow(0 0 0.15rem black);
            filter: drop-shadow(0 0 0.15rem black);
}

.image-tile {
    -webkit-transition: -webkit-transform .15s;
    transition: -webkit-transform .15s;
    transition: transform .15s;
    transition: transform .15s, -webkit-transform .15s;
    -webkit-filter: drop-shadow(0 0 0.07rem black);
            filter: drop-shadow(0 0 0.07rem black);
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
}

.home-panel img {
    -webkit-filter: none;
            filter: none
}

.image-tile:hover {
    -webkit-transform: scale(1.025) translateZ(0);
            transform: scale(1.025) translateZ(0);
    cursor: pointer;
}

.undecorated-image {
    -webkit-filter: drop-shadow(0 0 0.05rem black);
            filter: drop-shadow(0 0 0.05rem black);
}

.avatar img {
    -webkit-filter: none;
            filter: none
}
.pointer-hover:hover {
	cursor: pointer;
}
