            flex-wrap: wrap;
}

.home-wrapper {
    display: grid;
    margin: 1em auto;
    grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
    grid-auto-rows: minmax(225px, auto);
}

.home-panel {
    /* needed for the flex layout*/
    margin-left: 5px;
    margin-right: 5px;
    -webkit-flex: 1 1 130px;
            flex: 1 1 130px;
}

.home-header,
.home-footer {
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
    width: 70%;
}

.home-wrapper>* {
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}

